import { apiCall } from "@/setup/webApi";

export const members = (request) => {
  const queryString = new URLSearchParams(request).toString();

  return new Promise((resolve, reject) => {
    apiCall("get", `Export/members?${queryString}`, null, true)
      .then((response) => {
        resolve(response);
      })
      .catch((ex) => {
        reject(ex);
      });
  });
};