<template>
  <v-app>
    <v-navigation-drawer v-model="drawer" elevation="4" :rail="rail">
      <v-list-item :prepend-avatar="require(`@/assets/bgt_logo.jpg`)" title="Burhani Guards Pakistan" :subtitle="userName"
        nav class="my-2" />
      <v-divider></v-divider>
      <v-list density="compact" nav color="primary">
        <v-list-item prepend-icon="mdi-home" title="Home" to="/home" />
        <v-list-item prepend-icon="mdi-account-group" title="Members List" to="/members"
          v-if="isIncharge" />
        <v-divider class="mb-2"></v-divider>
        <v-list-subheader>Finance</v-list-subheader>
        <v-list-item prepend-icon="mdi-cash-multiple" title="Division" :href="financeLink('division')" target="_blank"
          v-if="isIncharge" />
        <v-list-item prepend-icon="mdi-cash-multiple" title="Personal" :href="financeLink('member')" target="_blank" />
        <template v-if="validateAuthOfficers">
          <v-divider class="mb-2" />
          <v-list-subheader>Forms Responses</v-list-subheader>
          <v-list-item prepend-icon="mdi-database-search" title="Member Upliftment Form" to="/upliftment-form-response" />
        </template>
        <!-- <v-list-item prepend-icon="mdi-format-list-text" title="Sharaf Forms" to="/sharaf-forms" /> -->
      </v-list>
    </v-navigation-drawer>
    <v-app-bar rounded :class="`ma-4 floating-app-bar ${mobile ? 'mobile' : ''} ${rail ? 'rail' : ''}`">
      <v-app-bar-nav-icon icon="mdi-menu" @click="showHideDrawer()" />
      <v-app-bar-title>
        {{ route.meta.title }}
      </v-app-bar-title>
      <template v-slot:append>
        <v-btn @click="logout" icon="mdi-logout"></v-btn>
      </template>
    </v-app-bar>
    <v-main style="--v-layout-top: 80px">
      <v-container fluid>
        <router-view v-slot="{ Component }">
          <component :is="Component"></component>
        </router-view>
      </v-container>
    </v-main>

    <snack-bar />
    <loading-dialog />
  </v-app>
</template>

<script setup>
import { ref, computed } from "vue";
import { useRoute } from "vue-router";
import { useDisplay } from 'vuetify';
// import { useRouter } from "vue-router";
import { useAuthStore } from "@/setup/stores/authStore";

const { mobile } = useDisplay();

const route = useRoute();
// const router = useRouter();
const authStore = useAuthStore();

const drawer = ref(mobile.value ? false : true);
const rail = ref(false);

function showHideDrawer() {
  if (mobile.value)
    drawer.value = !drawer.value;
  else
    rail.value = !rail.value;
}

const userName = computed(() => {
  if (authStore.user)
    return authStore.user.username;
  else
    return "N/A";
});

const isIncharge = computed(() => {
  if (authStore.user)
    return authStore.user.isIncharge;
  else
    return false;
});

function financeLink(type) {
  if (!authStore.user)
    return;

  var jwt = `${authStore.user.itsId}&&eca1811f-10d0-4e53-a017-43d78f138792&&${Date.now()}`;
  return `http://accounts.bgtpakistan.com/#/login?jwt=${btoa(jwt)}&type=${type}`;
}

function logout() {
  authStore.logout();
}

const authOfficers = ref([
  40496719,
  40476109,
  40419031,
  40480238,
  40452802,
  40480239,
  40496185
]);

const validateAuthOfficers = computed(() => {
  if (!!authStore.user && !!authStore.user.itsId)
    return authOfficers.value.includes(authStore.user.itsId);
  else
    return false;
});

// console.log(authStore.user);

</script>

<style>
.floating-app-bar {
  width: calc(100% - 288px) !important;
}

.floating-app-bar.mobile {
  width: calc(100% - 32px) !important;
}

.floating-app-bar.rail {
  width: calc(100% - 88px) !important;
}
</style>
