import { apiCall } from "@/setup/webApi";

export const getOrganizations = () => {
  return new Promise((resolve, reject) => {
    apiCall("get", `Utilities/orgs`)
      .then((response) => {
        resolve(response);
      })
      .catch((ex) => {
        reject(ex);
      });
  });
};

